.dash{
    position: relative;
}
.dash:after{
    content: "";
    position: absolute;
    top: 8px;
    right: 0rem;
    height: 70%;
    width: 1px;
    background-color: #19aba7;
}
@media all and (min-width: 320px) and (max-width: 768px){
    .header{
        padding-left: 1px!important;
        padding-right: 1px!important;
    }
    .logo{
        width: 50%;
    }
    .nav{
        display: none;
    }
    .call{
        display: block!important;
    }
    .third{
        margin-top: 1rem;
    }
}